import './App.css';
import fireStore from './firebase/config';
import {useEffect, useState } from 'react';

function App() {
  const [doc, setDoc] = useState({});

  useEffect(() => {
    const unsub = fireStore.collection('test')
      .onSnapshot((snap)=>{
        const doc = snap.docs[0];
        setDoc({id:doc.id, ...doc.data()})
      })
    return () => unsub()
  }, [])

  const toggleButton = () =>{
      fireStore.collection("test").doc(doc.id).update({isOn: !doc.isOn});
  }

  return (
    <div className="App">
        <button  onClick={()=>toggleButton()}>{doc.isOn ? "On" : "Off"}</button>
    </div>
  );
}

export default App;
